import { Box, Grid, Typography } from "@material-ui/core";
import { RouteComponentProps } from "@reach/router";
import React, { ReactElement } from "react";
import { UserFactory } from "../../factories/UserFactory";
import { IToolboxData } from "../../interfaces/IToolboxData";
import { ToolboxSetter } from "../../types/ToolboxSetter";
import NiceBox from "../common/NiceBox";
import RequestGridCard from "../user/RequestGridCard";
import _ from "lodash";

interface Props extends RouteComponentProps {
  toolboxData: IToolboxData;
  setToolbox: ToolboxSetter;
}

export default function MyRequestsPage(props: Props): ReactElement {
  const user = props.toolboxData.user
    ? props.toolboxData.user
    : UserFactory.build();

    const requests = _.orderBy(user.requests,["status.code"]);

  return (
    <Box>
      <Box py={2}>
      <Typography variant="h4">My Requests</Typography>
      </Box>
      <Typography variant="h6">
        Logged in as <strong>{user.fullName}</strong>
      </Typography>
      <NiceBox>
        <Grid container spacing={1}>
          {requests.map((request) => (
            <RequestGridCard request={request} />
          ))}
        </Grid>
      </NiceBox>
    </Box>
  );
}
