import { Box, Grid } from "@material-ui/core";
import { Router } from "@reach/router";
import React, { ReactElement } from "react";
import IPage from "../../interfaces/IPage";
import SideFilterContainer from "../side-filter/SideFilterContainer";
import AgenciesPage from "../pages/AgenciesPage";
import AgencyPage from "../pages/AgencyPage";
import WorkerDrawerPage from "../pages/WorkerDrawerPage";
import SideFilterHidden from "../side-filter/SideFilterHidden";


interface Props extends IPage {}


export default function AgenciesDataProvider(props: Props): ReactElement {

  const isMd = props.isMd;
  const toRender = props.toolboxData?.agencies ? (
    <Router primary={false}>
      <AgenciesPage
        agencies={props.toolboxData.agenciesContract}
        path="contract"
        showContractInfo={true}
        toolboxData={props.toolboxData}
      />
      <AgenciesPage
        agencies={props.toolboxData.agenciesLeasedHourly}
        path="leased-hourly"
        showRentedInfo={true}
        toolboxData={props.toolboxData}
      />
      <AgenciesPage
        agencies={props.toolboxData.agenciesLeasedMonthly}
        path="leased-monthly"
        showRentedInfo={true}
        toolboxData={props.toolboxData}
      />
      <AgencyPage
        agencies={props.toolboxData.agencies}
        showTwoYearTabs={true}
        path="contract/:agencyId"
        toolboxData={props.toolboxData}
        setToolbox={props.setToolbox}
      />
      <AgencyPage
        agencies={props.toolboxData.agencies}
        showRentalTabs={true}
        path="leased/:agencyId"
        toolboxData={props.toolboxData}
        setToolbox={props.setToolbox}
      />
      <AgencyPage
        agencies={props.toolboxData.agenciesLeasedHourly}
        showRentalTabs={true}
        path="leased-hourly/:agencyId"
        toolboxData={props.toolboxData}
        setToolbox={props.setToolbox}
      />
      <AgencyPage
        agencies={props.toolboxData.agenciesLeasedMonthly}
        showRentalTabs={true}
        path="leased-monthly/:agencyId"
        toolboxData={props.toolboxData}
        setToolbox={props.setToolbox}
      />
      <WorkerDrawerPage
        workersTwoYear={props.toolboxData.workersTwoYears}
        path="contract/:agencyId/worker-details/:workerTwoYearId"
        toolboxData={props.toolboxData}
        setToolbox={props.setToolbox}
      />
    </Router>
  ) : null;

  return (
    <Grid container spacing={0}>
      <Grid item xs={12} md={3}>
        <Box pr={isMd ? 2 : 0}>
          <Router primary={false}>
            <SideFilterHidden path="contract/:agencyId/worker-details/:workerTwoYearId" />
            <SideFilterContainer
              default
              is2Year={true}
              isLeased={false}
              isMd={isMd}
            />
            <SideFilterContainer
              path="leased-monthly/*"
              is2Year={false}
              isLeased={true}
              isMd={isMd}
            />
            <SideFilterContainer
              path="leased-hourly/*"
              is2Year={false}
              isLeased={true}
              isMd={isMd}
            />
          </Router>
        </Box>
      </Grid>
      <Grid item xs={12} md={9}>
        {toRender}
      </Grid>
    </Grid>
  );
}
