import React, { ReactElement, useState } from "react";
import { useTranslation } from "react-i18next";
import { demoAgencies } from "../../config/DemoItems";
import { DemoTransformUser } from "../../etl/DemoTransformUser";
import MainTransformer from "../../etl/MainTransformer";
import AgencyFactory from "../../factories/AgencyFactory";
import { UserFactory } from "../../factories/UserFactory";
import { IToolboxData } from "../../interfaces/IToolboxData";
import IWorkerTwoYear from "../../interfaces/IWorkerTwoYear";
import { ToolboxSetter } from "../../types/ToolboxSetter";
import DefaultLayout from "./DefaultLayout";
import _ from "lodash";
import {
  AgenciesHourlyOnly,
  AgenciesMonthlyOnly,
} from "../../etl/AgenciesLeasedFilters";
import IWorkerRental from "../../interfaces/IWorkerRental";

interface Props {
  classes: any;
  toolboxData: IToolboxData;
  setToolbox: ToolboxSetter;
}

export default function AppDataProvider(props: Props): ReactElement {
  const { t, i18n } = useTranslation();
  const [dataloaded, setDataloaded] = useState(false);

  if (dataloaded === false) {
    let agencies = AgencyFactory.buildList(demoAgencies.length);
    MainTransformer(agencies, { t, i18n });

    let workersTwoYear: IWorkerTwoYear[] = [];
    let workersRentalHour: IWorkerRental[] = [];
    let workersRentalMonth: IWorkerRental[] = [];

    // console.table(agencies);

    let agenciesContract = _.orderBy(
      agencies.filter((agency) => {
        return agency.hasTwoYearWorkerServices;
      }),
      ["rating"],
      ["desc"]
    );

    let agenciesLeased = _.orderBy(
      agencies.filter((agency) => {
        return agency.hasRentedServices;
      }),
      ["rating"],
      ["desc"]
    );

    let agenciesLeasedHourly = AgenciesHourlyOnly(agenciesLeased);
    let agenciesLeasedMonthly = AgenciesMonthlyOnly(agenciesLeased);

    agenciesContract.forEach((agency) => {
      if (agency.workersTwoYear) {
        workersTwoYear = workersTwoYear.concat(agency.workersTwoYear);
      }
    });

    agenciesLeasedHourly.forEach((agency) => {
      agency.servicesSelectableWorkers?.forEach((service) => {
        workersRentalHour = workersRentalHour.concat(service.workersRental);
      });
    });

    let user = UserFactory.build();
    DemoTransformUser(user, workersTwoYear, workersRentalHour);

    props.setToolbox((prevState) => ({
      ...prevState,
      demoUser: user,
      user: user,
      agencies: agencies,
      agenciesContract: agenciesContract,
      agenciesLeasedHourly: agenciesLeasedHourly,
      agenciesLeasedMonthly: agenciesLeasedMonthly,
      workersTwoYears: workersTwoYear,
      workersRentalHour: workersRentalHour,
      workersRentalMonth: workersRentalMonth,
    }));

    setDataloaded(true);
  }
  return (
    <DefaultLayout
      classes={props.classes}
      toolboxData={props.toolboxData}
      setToolbox={props.setToolbox}
    />
  );
}
