import { Box, Container } from "@material-ui/core";
import Branding from "../../config/Branding";
import SiteHeadLarge from "./SiteHeadLarge";
import { Router } from "@reach/router";
import LandingPage from "../pages/LandingPage";
import SiteHead from "./SiteHead";
import AgenciesDataProvider from "./AgenciesDataProvider";
import LoginPage from "../pages/LoginPage";
import RequestWorkerPage from "../pages/RequestWorkerPage";
import SuccessPage from "../pages/SuccessPage";
import RequestConfirmationPage from "../pages/RequestConfirmationPage";
import FilterPage from "../pages/FilterPage";
import SiteHeadless from "./SiteHeadless";
import { withBreakpoints } from "react-breakpoints";
import { IToolboxData } from "../../interfaces/IToolboxData";
import { ToolboxSetter } from "../../types/ToolboxSetter";
import MyRequestsPage from "../pages/MyRequestsPage";
import SelectSchedulePage from "../pages/SelectSchedulePage";

const DefaultLayout = (props: {
  classes: any;
  breakpoints: any;
  currentBreakpoint: any;
  toolboxData: IToolboxData;
  setToolbox: ToolboxSetter;
}) => {
  const { breakpoints, currentBreakpoint } = props;
  const isMd = breakpoints[currentBreakpoint] >= breakpoints.md;
  const burgerWidth = 26;

  return (
    <Box bgcolor={Branding.lightBg}>
      <Router primary={false}>
        <SiteHead
          default
          isMd={isMd}
          burgerWidth={burgerWidth}
          toolboxData={props.toolboxData}
        />
        <SiteHeadLarge
          path="/start/*"
          isMd={isMd}
          burgerWidth={burgerWidth}
          toolboxData={props.toolboxData}
        />
        <SiteHeadLarge
          path="/"
          isMd={isMd}
          burgerWidth={burgerWidth}
          toolboxData={props.toolboxData}
        />
        <SiteHeadless
          path="agencies/contract-filters"
          showCancelText={true}
          title="Filters"
          rightText="Clear All"
        />
        <SiteHeadless
          path="agencies/leased-filters"
          showCancelText={true}
          title="Filters"
        />
      </Router>
      <Box id="page-content-container" style={{ height: "100%" }}>
        <Container>
          <Router primary={false}>
            <LandingPage path="/" isMd={isMd} />
            <LandingPage path="start/*" isMd={isMd} />
            <AgenciesDataProvider
              isMd={isMd}
              path="agencies/*"
              toolboxData={props.toolboxData}
              setToolbox={props.setToolbox}
              // translationPack={{ t, i18n }}
            />
            <FilterPage path="agencies/contract-filters" is2Year={true} />
            <FilterPage path="agencies/leased-filters" isLeased={true} />
            <LoginPage path="/login" demoLink="/new-request/two-year-worker" />
            <LoginPage
              path="/login/lease"
              demoLink="/new-request/two-year-worker"
            />
            <LoginPage
              path="/login/month"
              demoLink="/new-request/two-year-worker"
            />
            <SelectSchedulePage
              path="/new-request/select-hours"
              link="/new-request/hourly-worker"
              isHourly={true}
              toolboxData={props.toolboxData}
              setToolbox={props.setToolbox}
            />
            <SelectSchedulePage
              path="/new-request/select-months"
              link="/new-request/monthly-worker"
              isHourly={false}
              toolboxData={props.toolboxData}
              setToolbox={props.setToolbox}
            />
            <RequestWorkerPage
              path="/new-request/two-year-worker"
              isTwoYear={true}
              link="/new-request/confirm"
              isMd={isMd}
              toolboxData={props.toolboxData}
              setToolbox={props.setToolbox}
            />
            <RequestWorkerPage
              path="/new-request/hourly-worker"
              isRentalHourly={true}
              link="/new-request/confirm-lease"
              isMd={isMd}
              toolboxData={props.toolboxData}
              setToolbox={props.setToolbox}
            />
            <RequestWorkerPage
              path="/new-request/monthly-worker"
              isRentalMonthly={true}
              link="/new-request/confirm-lease"
              isMd={isMd}
              toolboxData={props.toolboxData}
              setToolbox={props.setToolbox}
            />
            <RequestConfirmationPage
              path="/new-request/confirm"
              link="/success"
              toolboxData={props.toolboxData}
              setToolbox={props.setToolbox}
            />
            <RequestConfirmationPage
              path="/new-request/confirm-lease"
              link="/success-lease"
              toolboxData={props.toolboxData}
              setToolbox={props.setToolbox}
            />
            <SuccessPage
              path="/success"
              message="Your request has been sent."
              smallMessage="The agency will contact you within 1 business day."
              toolboxData={props.toolboxData}
              setToolbox={props.setToolbox}
            />
            <SuccessPage
              path="/success-lease"
              message="Your booking is completed."
              smallMessage="The agency may contact you if further details are required."
              toolboxData={props.toolboxData}
              setToolbox={props.setToolbox}
            />

            <MyRequestsPage
              path="/account/my-requests"
              toolboxData={props.toolboxData}
              setToolbox={props.setToolbox}
            />
          </Router>
        </Container>
      </Box>
    </Box>
  );
};

export default withBreakpoints(DefaultLayout);
