import AgencyCard from "../agency/AgencyCard";

import React, { Component } from "react";
import IAgenciesPage from "../../interfaces/IAgenciesPage";
import ScrollToTop from "../common/ScrollToTop";

class AgenciesPage extends Component<IAgenciesPage, any> {
  render() {
    let agencies;
    if (this.props.agencies !== undefined) {
      if (this.props.toolboxData !== undefined) {
        let toolbox = this.props.toolboxData;
        agencies = this.props.agencies.map((agency) => (
          <AgencyCard
            key={agency.id}
            agency={agency}
            showContractInfo={this.props.showContractInfo}
            showTabChips={this.props.showRentedInfo}
            toolboxData={toolbox}
          />
        ));
      }
    } else {
      agencies = <React.Fragment>No agencies</React.Fragment>;
    }

    return (
      <React.Fragment>
        <ScrollToTop />
        {agencies}
      </React.Fragment>
    );
  }
}

export default AgenciesPage;
