import { Grid } from "@material-ui/core";
import React from "react";
import IAgencyTab from "../../interfaces/IAgencyTab";
import { IToolboxData } from "../../interfaces/IToolboxData";
import { ToolboxSetter } from "../../types/ToolboxSetter";
import WorkerRentalGridCard from "../worker/rental/WorkerRentalGridCard";
import Worker2CardWrapper from "../worker/two-years/Worker2CardWrapper";

const AgencyTabPanelGridDesign = (props: {
  tab: IAgencyTab;
  isHourly: boolean;
  isMonthly: boolean;
  toolboxData: IToolboxData;
  setToolbox: ToolboxSetter;
}) => {
  return (
    <Grid container spacing={1}>
      {props.tab.workersTwoYear?.map((worker) => (
        <Worker2CardWrapper
          worker={worker}
          design="grid"
          toolboxData={props.toolboxData}
          setToolbox={props.setToolbox}
        />
      ))}
      {props.tab.workersRental?.map((worker) => (
        <WorkerRentalGridCard
          worker={worker}
          isHourly={props.isHourly}
          isMonthly={props.isMonthly}
          showButtons={true}
          toolboxData={props.toolboxData}
          setToolbox={props.setToolbox}
        />
      ))}
    </Grid>
  );
};

export default AgencyTabPanelGridDesign;
