import { Box } from "@material-ui/core";
import { RouteComponentProps } from "@reach/router";
import React, { Component } from "react";
import IAgency from "../../interfaces/IAgency";
import { IToolboxData } from "../../interfaces/IToolboxData";
import { ToolboxSetter } from "../../types/ToolboxSetter";
import AgencyDetails from "../agency/AgencyDetails";
import ScrollToTop from "../common/ScrollToTop";

interface IAgencyPageParams {
  agencyId: string;
  showTwoYearTabs: boolean;
  showRentalTabs: boolean;
  agencies: IAgency[];
  toolboxData:IToolboxData;
  setToolbox:ToolboxSetter;
}

interface IAgencyPage extends RouteComponentProps<IAgencyPageParams> {}

class AgencyPage extends Component<IAgencyPage, any> {
  render() {
    let agency = null;
    let showTwoYearTabs = this.props.showTwoYearTabs
      ? this.props.showTwoYearTabs
      : false;
    let showRentalTabs = this.props.showRentalTabs
      ? this.props.showRentalTabs
      : false;

    if (this.props.agencyId && this.props.agencies)
      if (this.props.agencies.length > 0) {
        const agencyId = parseInt(this.props.agencyId);
        agency = this.props.agencies?.filter((x) => x.id === agencyId)[0];

        if (agency && this.props.setToolbox && this.props.toolboxData) {
          return (
            <React.Fragment>
              <ScrollToTop />
              <AgencyDetails
                agency={agency}
                showTwoYearTabs={showTwoYearTabs}
                showRentalTabs={showRentalTabs}
                toolboxData={this.props.toolboxData}
                setToolbox={this.props.setToolbox}
              />
            </React.Fragment>
          );
        }
      }
    return <Box>Loading..</Box>;
  }
}

export default AgencyPage;
