import { navigate, RouteComponentProps } from "@reach/router";
import React from "react";
import { IToolboxData } from "../../interfaces/IToolboxData";
import { ToolboxSetter } from "../../types/ToolboxSetter";
import IWorkerTwoYear from "../../interfaces/IWorkerTwoYear";
import ScrollToTop from "../common/ScrollToTop";
import Worker2Details from "../worker/two-years/Worker2Details";

interface IWorkerDrawerPageParams {
  workerTwoYearId: string;
  workersTwoYear: IWorkerTwoYear[];
  onReserveButton: React.MouseEventHandler<HTMLButtonElement>;
  toolboxData: IToolboxData;
  setToolbox: ToolboxSetter;
}

interface IWorkerDrawerPage
  extends RouteComponentProps<IWorkerDrawerPageParams> {}

const WorkerDrawerPage = (props: IWorkerDrawerPage) => {
  const workersTwoYear = props.workersTwoYear ? props.workersTwoYear : [];
  const workerId = props.workerTwoYearId ? parseInt(props.workerTwoYearId) : 0;
  const worker = workersTwoYear.find((worker) => worker.id === workerId);
  const setToolbox = props.setToolbox ? props.setToolbox : () => {};
  let onReserveButton: React.MouseEventHandler<HTMLButtonElement> = () => {};
  if (props.setToolbox) {
    onReserveButton = props.onReserveButton
      ? props.onReserveButton
      : () => {
          setToolbox((prevState) => ({
            ...prevState,
            selectedWorkerTwoYear: worker,
          }));
          navigate("/login");
        };
  }

  return (
    <>
      <ScrollToTop />
      {worker && props.setToolbox && (
        <Worker2Details worker={worker} onReserveButton={onReserveButton} />
      )}
    </>
  );
};

export default WorkerDrawerPage;
