import { Box, Grid } from "@material-ui/core";
import { RouteComponentProps } from "@reach/router";
import React from "react";
import WizardCard from "./WizardCard";
import { useTranslation } from "react-i18next";

interface Params {
  isMd:boolean
}

interface Props extends RouteComponentProps<Params> {}

export const WizardLand = (props: Props) => {
  const { t } = useTranslation();

  const xs = 6;
  const md = 4;

  // const rentalLink = "/agencies/leased";
  const rentalLink = "/start/lease";
  const isMd = props.isMd !== undefined? props.isMd : false;

  return (
    <Box textAlign="center">
      <Grid container justify="center" spacing={3}>
        <Grid item xs={xs} md={md}>
          <WizardCard isMd={isMd} to="/agencies/contract" icon="perm_contact_calendar_icon">
            {t('2-years-contract')}
          </WizardCard>
        </Grid>
        <Grid item xs={xs} md={md}>
          <WizardCard isMd={isMd} to={rentalLink + "-hourly"} icon="schedule">
            {t('lease-by-hour')}
          </WizardCard>
        </Grid>
        <Grid item xs={xs} md={md}>
          <WizardCard isMd={isMd} to={rentalLink + "-monthly"} icon="date_range">
          {t('lease-by-month')}
          </WizardCard>
        </Grid>
        {/* <Grid item xs={xs} md={md}>
            <WizardCard to="/agencies" icon="business">Agencies in Bahrain</WizardCard>
          </Grid> */}
      </Grid>
    </Box>
  );
};
