import FormatCurrency from "../helpers/FormatCurrency";
import IAgency from "../interfaces/IAgency";
import Faker from "faker";
import INationality from "../interfaces/INationality";


export function TransformNationalities(
  agency: IAgency,
  nationality: INationality
) {
  if (nationality.titleWithFees === undefined) {
    let fees: number;
    let feesUnit: string;

    if (agency.hasRentedServices) {
      fees = Faker.random.number({
        min: 2,
        max: 5,
      });
      feesUnit = "BD/Hr";
    } else {
      fees = 1100;
      feesUnit = "BD";
    }
    let feesFormatted = " (" + FormatCurrency(fees, 0) + " " + feesUnit + ")";
    nationality.titleWithFees = nationality.title + feesFormatted;
  }
}
