import {
  Badge,
  Box,
  Divider,
  Grid,
  GridJustification,
  Icon,
} from "@material-ui/core";
import React, { ReactElement, useState } from "react";
import { useTranslation } from "react-i18next";
import { TopMenuType } from "../../types/TopMenuType";
import TopMenuItem from "./TopMenuItem";
import { slide as Menu, State } from "react-burger-menu";
import Branding from "../../config/Branding";
import BurgerItem from "./BurgerItem";
import Logo from "./Logo";
import { IToolboxData } from "../../interfaces/IToolboxData";
import Faker from "faker";

interface Props {
  isBurger: boolean;
  burgerWidth?: number;
  burgerLeft?: number;
  burgerTop?: number;
  justify?: GridJustification;
  toolboxData: IToolboxData;
}

export default function TopMenu(props: Props): ReactElement {
  const { t, i18n } = useTranslation();
  const isEnglish = i18n.language === "en";
  const langTitle = isEnglish ? "" : "English";
  const targetLang = isEnglish ? "ar" : "en";

  const burgerWidth = props.burgerWidth ? props.burgerWidth : 26;
  const burgerTop = props.burgerTop ? props.burgerTop : 26;
  const burgerLeft = props.burgerLeft ? props.burgerLeft : 24;

  const myRequestsLink = "/account/my-requests";

  let countRequests = 0;

  if (props.toolboxData.user !== undefined) {
    countRequests = props.toolboxData.user.requests.length;
  }

  const [isOpen, setIsOpen] = useState(false);

  const handleStateChange = (state: State) => {
    setIsOpen(state.isOpen);
  };

  const burgerStyles = {
    bmBurgerButton: {
      position: "absolute",
      width: burgerWidth + "px",
      height: "20px",
      left: burgerLeft + "px",
      top: burgerTop + "px",
    },
    bmBurgerBars: {
      background: "#373a47",
    },
    bmBurgerBarsHover: {
      background: "#a90000",
    },
    bmCrossButton: {
      height: "24px",
      width: "24px",
    },
    bmCross: {
      background: Branding.primary,
    },
    bmMenuWrap: {
      position: "fixed",
      height: "100%",
    },
    bmMenu: {
      background: Branding.lightBg + "f4",
      padding: "0.5em 1.5em 0",
      fontSize: "1.15em",
    },
    bmMorphShape: {
      fill: "#373a47",
    },
    bmItemList: {
      color: "#b8b7ad",
      padding: "0.8em",
      height: "auto",
    },
    // bmItem: {
    //   display: "block",
    //   marginBottom: 16,
    // },
    bmOverlay: {
      background: "rgba(0, 0, 0, 0.3)",
    },
  };

  const topMenu: TopMenuType = [
    // {
    //   title: t("safety"),
    //   to: "#",
    // },
    {
      title: t("login"),
      to: myRequestsLink,
      prominent: true,
    },
    {
      node: (
        <Badge
          badgeContent={Faker.random.number({ min: 1, max: 9 })}
          color="primary"
        >
          <Icon fontSize="small">favorite_outlined</Icon>
        </Badge>
      ),
    },
    {
      title: t("my-requests"),
      to: myRequestsLink,
      count: countRequests,
      badgeOverlap: "circle",
    },
    {
      title: t("our-policy"),
    },
    {
      title: t("contact-us"),
    },
    {
      title: langTitle,
      onClick: () => {
        i18n.changeLanguage(targetLang);
      },
    },
  ];
  return (
    <React.Fragment>
      {props.isBurger && (
        <Menu
          styles={burgerStyles}
          pageWrapId={"page-content-container"}
          outerContainerId={"app-root"}
          isOpen={isOpen}
          onStateChange={(state) => handleStateChange(state)}
        >
          <Box>
            <Logo marginLeft={0} maxHeight={40} />
          </Box>
          <Box pt={2} pb={1}>
            <Divider />
          </Box>
          {topMenu.map((nav) => (
            <BurgerItem item={nav} setIsOpen={setIsOpen} />
          ))}
        </Menu>
      )}
      {!props.isBurger && (
        <Grid container justify={props.justify} direction="row-reverse">
          {topMenu.map((nav) => (
            <Grid item>
              <TopMenuItem item={nav} />
            </Grid>
          ))}
        </Grid>
      )}
    </React.Fragment>
  );
}
