import IAgency from "../interfaces/IAgency";
import _ from "lodash";
import WorkerTwoYearFactory from "../factories/WorkerTwoYearFactory";
import Faker from "faker";
import {
  demoAgencies,
  demoRentalMonthlyRemarks,
  demoRentalHourlyRemarks,
  demoNumberOfContractWorkersPerAgency,
} from "../config/DemoItems";
import { TransformTwoYearWorker } from "./TransformTwoYearWorker";
import { TransformNationalities } from "./TransformNationalities";
import { MakeTabsTwoYearWorkers } from "./MakeTabsTwoYearWorkers";
import { MakeTabsServices } from "./MakeTabsServices";
import { MakeTabsRentalWorkers } from "./MakeTabsRentalWorkers";
import { TransformRentalWorker } from "./TransformRentalWorker";
import { TranslationPack } from "../types/TranslationPack";

export function TransformAgency(
  agency: IAgency,
  index: number,
  translate: TranslationPack
) {
  // agency.ratingHtml = TransformRatingText(agency.rating);
  let tabsIndex = 0;

  if (agency.demoId === 0) {
    agency.demoId = index;
    let demoAgency = demoAgencies.find((x) => x.id === agency.demoId);
    if (demoAgency) {
      agency.logo = "/img/agencies/" + agency.demoId + ".png";
      agency.title = demoAgency.name;
      agency.titleAr = demoAgency.nameAr;
    } else {
      agency.title = Faker.company.companyName();
    }
  }

  agency.showReviewsRatings = agency.rating >= 3;

  if (agency.servicesSelectableWorkers) {
    agency.servicesSelectableWorkers.forEach((service) => {
      if (service.isTwoYearContract) {
        agency.hasTwoYearWorkerServices = true;
      }
      if (
        service.isRentalHourly ||
        service.isRentalMonthly ||
        service.isRentalMonthlyStayOver
      ) {
        agency.hasRentedServices = true;
        service.workersRental = _.orderBy(
          service.workersRental,
          ["rating"],
          ["desc"]
        );
      }
      if (service.isRentalHourly) {
        service.remarks = Faker.random.arrayElement(demoRentalHourlyRemarks);
      }
      if (service.isRentalMonthly) {
        service.remarks = Faker.random.arrayElement(demoRentalMonthlyRemarks);
      }
    });
  }

  agency.nationalities?.forEach((nationality) =>
    TransformNationalities(agency, nationality)
  );

  if (agency.hasTwoYearWorkerServices) {
    agency.workersTwoYear = WorkerTwoYearFactory.buildList(
      Faker.random.number(demoNumberOfContractWorkersPerAgency)
    );
    agency.workersTwoYear.forEach((worker) => {
      TransformTwoYearWorker(worker, agency, translate);
    });
    tabsIndex = MakeTabsTwoYearWorkers(agency, tabsIndex);
  }

  if (agency.hasRentedServices) {
    agency.servicesSelectableWorkers?.forEach((service) => {
      service.workersRental.forEach((worker) => {
        TransformRentalWorker(worker, service, agency, translate);
      });
    });
    tabsIndex = MakeTabsRentalWorkers(agency, tabsIndex);
  }

  if (agency.services) {
    MakeTabsServices(agency, tabsIndex);
  }
}
