import { Icon } from "@material-ui/core";
import Tooltip from "@material-ui/core/Tooltip";
import React from "react";
import SmallText from "../common/SmallText";



const IconInfo = (props: {
  icon?: string;
  iconFontSize?: "inherit" | "default" | "small" | "large";
  children: React.ReactNode;
  title?: string;
  style?: React.CSSProperties;
}) => {
  const iconFontSize = props.iconFontSize ? props.iconFontSize : "inherit";

  const inside = (
    <SmallText style={props.style}>
      {props.icon && (
        <React.Fragment>
          <Icon fontSize={iconFontSize}>{props.icon}</Icon>&nbsp;
        </React.Fragment>
      )}
      {props.children}
    </SmallText>
  );


  return (
    <React.Fragment>
      {props.title !== undefined && (
        <Tooltip title={props.title} arrow>
          <div>{inside}</div>
        </Tooltip>
      )}
      {props.title === undefined && inside}
    </React.Fragment>
  );
};

export default IconInfo;
