import React, { ReactElement, useState } from "react";
import { MakeToolboxData } from "../../config/MakeToolbox";
import AppDataProvider from "./AppDataProvider";

interface Props {
  classes: any;
}

export default function ToolboxState(props: Props): ReactElement {
  const [toolboxData, setToolbox] = useState(MakeToolboxData());

  return (
    <AppDataProvider
      classes={props.classes}
      toolboxData={toolboxData}
      setToolbox={setToolbox}
    />
  );
}
