import {
  Avatar,
  Box,
  Button,
  Divider,
  Grid,
  Typography,
} from "@material-ui/core";
import React, { ReactElement } from "react";
import { useTranslation } from "react-i18next";
import Branding from "../../config/Branding";
import { IRequest } from "../../interfaces/IRequest";
import NiceBox from "../common/NiceBox";
import IconInfo from "../worker/IconInfo";

interface Props {
  request: IRequest;
}

export default function RequestGridCard(props: Props): ReactElement {
  const { t } = useTranslation();

  const picWidth = "92%";
  const picHeight = 90;

  const request = props.request;
  const worker = request.worker;

  let serviceText = "";

  if (request.isTwoYearContract) serviceText = t("2-years-contract");
  if (request.isLeaseByMonth) serviceText = t("lease-by-month");
  if (request.isLeaseByHour) serviceText = t("lease-by-hour");

  if (worker === undefined) return <React.Fragment></React.Fragment>;
  return (
    <Grid item md={4} xs={12} lg={3}>
      <NiceBox p={1} my={0}>
        <Box>
          <Typography
            style={{
              textAlign: "center",
              padding: 4,
              marginBottom: 8,
              background: request.status.bgColor,
              borderRadius: Branding.borderRadius.niceRadius,
            }}
          >
            {request.status.title}
          </Typography>

          <Typography style={{ fontWeight: "bold" }}>{worker?.name}</Typography>
          <Box py={1}>
            <Divider />
          </Box>
          <Grid container spacing={0}>
            <Grid xs={5}>
              <Avatar
                src={worker.smallPicture}
                style={{
                  width: picWidth,
                  minHeight: picHeight,
                  alignSelf: "center",
                  paddingLeft: "4%",
                  paddingRight: "4%",
                }}
                variant="rounded"
              />
            </Grid>
            <Grid xs={7}>
              <Box minHeight={140}>
              <IconInfo title="Agency">{worker.agency?.title}</IconInfo>
              <IconInfo icon="work" title="Job Title">
                {worker.jobTitle}
              </IconInfo>
              <IconInfo icon="flag" title="Nationality">
                {worker.nationality.title}
              </IconInfo>
              <IconInfo title="Service">{serviceText}</IconInfo>
              <IconInfo title="Address">
                {request.applicationForm.address}
              </IconInfo>
              {/* <IconInfo title="Timing">
                <strong>{request.applicationForm.selectedHours}&nbsp;</strong>
              </IconInfo> */}
              </Box>
            </Grid>
          </Grid>
          <Box py={1}>
            <Divider />
          </Box>
          <Grid container justify="center" spacing={1}>
            {true && (
              <Grid item>
                <Button variant="outlined" color="primary">
                  Details
                </Button>
              </Grid>
            )}
            {request.status.allowCancel && (
              <Grid item>
                <Button variant="outlined" color="primary">
                  Cancel
                </Button>
              </Grid>
            )}
          </Grid>
        </Box>
      </NiceBox>
    </Grid>
  );
}
