import { Avatar, Box, Grid, Typography } from "@material-ui/core";
import React from "react";
import IAgency from "../../interfaces/IAgency";
import SmallText from "../common/SmallText";
import { Link } from "@reach/router";
import NiceBox from "../common/NiceBox";
import Branding from "../../config/Branding";
import AgencyRatingInfo from "./AgencyRatingInfo";
import FavoriteButton from "../common/FavoriteButton";
import OrangeChip from "../common/OrangeChip";
import AgencyTabRentalChips from "./AgencyTabRentalChips";
import CardChips from "../common/CardChips";
import { IToolboxData } from "../../interfaces/IToolboxData";
import { useTranslation } from "react-i18next";

const AgencyCard = (props: {
  agency: IAgency;
  showContractInfo?: boolean;
  showTabChips?: boolean;
  toolboxData: IToolboxData;
}) => {
  const {t, i18n} = useTranslation();
  const isArabic = i18n.language === "ar";
  const agency = props.agency;
  // const smallInfoGrid = 3;

  const newChip = agency.new ? <OrangeChip label="New" /> : null;

  let nationalitiesTitles = agency.nationalities?.map(
    (nationality) => t("nationality."+nationality.title.toLowerCase())
  );
  let nationalitiesText = nationalitiesTitles?.join(", ");

  const twoYearTabsTitles = agency.tabsTwoYearWorkers.map(
    (tab) => t("nationality." + tab.title.toLowerCase()) + " (" + tab.feesText + ")"
  );

  // let servicesText = serviceTitles.join(", ");

  return (
    <NiceBox>
      <Grid container spacing={2}>
        <Grid item xs={3} md={2}>
          <Box>
            <Link to={agency.id.toString()}>
              <Avatar
                variant="rounded"
                src={agency.logo}
                style={{
                  flexShrink: 0,
                  width: "100%",
                  minHeight: 109,
                }}
              />
            </Link>
          </Box>
        </Grid>
        <Grid item xs={9} md={8}>
          <Box pb={0}>
            <Typography style={{ fontSize: "110%", fontWeight: "bold" }}>
              <Link
                to={agency.id.toString()}
                style={{ textDecoration: "none" }}
              >
                {isArabic ? agency.titleAr : agency.title}
              </Link>{" "}
              {newChip}
            </Typography>
          </Box>
          {props.showContractInfo && (
            <Box>
              <SmallText>
                <CardChips titles={twoYearTabsTitles} variant="text" />
              </SmallText>
            </Box>
          )}

          {props.showTabChips && props.agency.servicesSelectableWorkers && (
            <Box>
              <SmallText>
                <AgencyTabRentalChips
                  services={props.agency.servicesSelectableWorkers}
                  showContract={false}
                  showRental={true}
                  renderAsText={true}
                  showPriceOnly={true}
                />
              </SmallText>
            </Box>
          )}
          {!props.showContractInfo && false && (
            <SmallText>{nationalitiesText}</SmallText>
          )}
          {!props.showContractInfo && (
            <SmallText>{props.agency.tabsRental[0].remarks}</SmallText>
          )}
          <AgencyRatingInfo agency={agency} />
          {/* <SmallText>&nbsp;</SmallText> */}
          {/* <AgencyContactInfo agency={agency} /> */}
        </Grid>
        <Grid item xs={12} md={2}>
          <Box textAlign="right" px={1} color={Branding.text.secondary}>
            <FavoriteButton />
          </Box>
        </Grid>
      </Grid>
    </NiceBox>
  );
};

export default AgencyCard;
