import { Box, Button, Typography } from "@material-ui/core";
import { RouteComponentProps } from "@reach/router";
import React, { ReactElement, useState } from "react";
import { IToolboxData } from "../../interfaces/IToolboxData";
import { ToolboxSetter } from "../../types/ToolboxSetter";
import RequestDaysScheduleDisplayMode from "../request/RequestDaysScheduleDisplayMode";
import RequestFrequency from "../request/RequestFrequency";
import WorkerRentalCardSlots from "../worker/rental/WorkerRentalCardSlots";
import { navigate } from "@reach/router";

interface Props extends RouteComponentProps {
  toolboxData: IToolboxData;
  setToolbox: ToolboxSetter;
  isHourly: boolean;
  link: string;
}

export default function SelectSchedulePage(props: Props): ReactElement {
  const selectScheduleLabel = props.isHourly
    ? "Select Timing"
    : "Select Months";

  const itemPluralLabel = props.isHourly ? "timings" : "months";
  const [maxDays, setMaxDays] = useState(1);

  return (
    <Box>
      {props.toolboxData.selectedWorkerRental !== undefined && (
        <Box>
          <Box my={2}>
            <Typography variant="h4">{selectScheduleLabel}</Typography>
          </Box>
          <Box my={2}>
            <Typography>
              You can select multiple {itemPluralLabel} from the below list:
            </Typography>
          </Box>
          <Box my={2}>
            {props.isHourly && (
              <RequestDaysScheduleDisplayMode
                setter={setMaxDays}
                numberOfMultiDays={8}
              />
            )}
          </Box>
          <WorkerRentalCardSlots
            isHourly={props.isHourly}
            isMonthly={!props.isHourly}
            worker={props.toolboxData.selectedWorkerRental}
            md={3}
            maxDays={maxDays}
            maxMonths={12}
          />

          <Box my={2}>{props.isHourly && <RequestFrequency />}</Box>
          <Box my={3} pb={5} textAlign="center">
            <Button
              variant="contained"
              color="primary"
              onClick={() => {
                navigate(props.link);
              }}
            >
              Continue
            </Button>
          </Box>
        </Box>
      )}
    </Box>
  );
}
