import { IUser } from "../interfaces/IUser";
import IWorkerRental from "../interfaces/IWorkerRental";
import IWorkerTwoYear from "../interfaces/IWorkerTwoYear";
import Faker from "faker";
import { IRequest } from "../interfaces/IRequest";
import { ApplicationFormFactory } from "../factories/ApplicationFormFactory";
import { statusCodesLease, statusCodesTwoYear } from "../config/StatusCodes";

export const DemoTransformUser = (
  user: IUser,
  workersTwoYear: IWorkerTwoYear[],
  workersRental: IWorkerRental[]
) => {
  const min = 1;
  const max = 3;
  const twoYear = Faker.random.arrayElements(
    workersTwoYear,
    Faker.random.number({ min: min, max: max })
  );
  const rental = Faker.random.arrayElements(
    workersRental,
    Faker.random.number({ min: min, max: max })
  );

  let requests: IRequest[] = [];

  twoYear.forEach((worker) => {
    let request: IRequest = {
      applicationForm: ApplicationFormFactory.build(),
      status: Faker.random.arrayElement(statusCodesTwoYear),
      worker: worker,
      isTwoYearContract: true,
      isLeaseByHour: false,
      isLeaseByMonth: false,
    };
    request.applicationForm.workerTwoYear = worker;
    requests.push(request);
  });

  rental.forEach((worker) => {
    let randomFlag = Faker.random.boolean();
    let request: IRequest = {
      applicationForm: ApplicationFormFactory.build(),
      status: Faker.random.arrayElement(statusCodesLease),
      worker: worker,
      isTwoYearContract: false,
      isLeaseByHour: randomFlag,
      isLeaseByMonth: !randomFlag,
    };
    request.applicationForm.workerRental = worker;
    requests.push(request);
  });

  user.requests = requests;
};
