import * as Factory from "factory.ts";
import Faker from "faker";
import { IApplicationForm } from "../interfaces/IApplicationForm";
import dateFormat from "dateformat";

export const ApplicationFormFactory = Factory.Sync.makeFactory<IApplicationForm>(
  {
    fullName: Factory.each(() => Faker.name.findName()),
    mobileNumber:
      "+973" +
      Factory.each(() => Faker.random.number({ min: 30000000, max: 79999999 })),
    address: Factory.each(() => Faker.address.streetAddress()),
    selectedHours: Factory.each(() =>
      Faker.random.arrayElements(
        [
          {
            key: dateFormat(Faker.date.soon(6), "ddd d-mmm"),
            value: "3:00pm - 5:00pm",
          },
          {
            key: dateFormat(Faker.date.soon(6), "ddd d-mmm"),
            value: "3:00pm - 5:00pm",
          },
          {
            key: dateFormat(Faker.date.soon(6), "ddd d-mmm"),
            value: "3:00pm - 5:00pm",
          },
        ],
        Faker.random.number({min:2,max:2})
      )
    ),
    selectedMonths: Factory.each(() =>
      Faker.random.arrayElements(
        [Faker.date.month(), Faker.date.month(), Faker.date.month()],
        Faker.random.number({min:1,max:2})
      )
    ),
    paymentSummaryItems: [],
    paymentRemarks: "",
    selectedHoursFrequency: Faker.random.arrayElement(["One Time", "Every Week"]),
  }
);
