import IAgency from "../interfaces/IAgency";
import { TranslationPack } from "../types/TranslationPack";
import { TransformAgency } from "./TransformAgency";

export default function MainTransformer(
  agencies: IAgency[],
  translationPack: TranslationPack
) {
  let i = 1;
  agencies.forEach((agency) => {
    TransformAgency(agency, i, translationPack);
    i++;
  });
}
